.stars {
  display: flex;
  justify-content: flex-start;
}
    
.stars .stars__star {
  background-image: url(/images/star.svg);
  background-repeat: no-repeat;
  background-position: center;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}
  
.stars--4 :nth-child(-n+4) {
  background-image: url(/images/star-active.svg);
}
